import React, { Component } from "react";
import { Table } from "reactstrap";

import Pick from "./Ship/Pick.js";
import Level from "./Ship/Level.js";
import Retrofit from "./Ship/Retrofit.js";
import Oath from "./Ship/Oath.js";

export default class Ship extends Component {
  render() {
    const ship_rarity         = this.props.ship_rarity;
    const ship_type           = this.props.ship_type;
    const azurapi             = this.props.azurapi;
    const ship_id             = this.props.ship_id;
    const get_ship_option     = this.props.get_ship_option;
    const ship_level          = this.props.ship_level;
    const get_level_option    = this.props.get_level_option;
    const ship_kai            = this.props.ship_kai;
    const get_retrofit_option = this.props.get_retrofit_option;
    const ship_oath           = this.props.ship_oath;
    const get_oath_option     = this.props.get_oath_option;

    return (
      <Table className="fleetModalTable">
        <thead>
          <tr>
            <th id="slot-ship-rarity" className="font16px" colSpan="2">{ship_rarity}</th>
            <th id="slot-ship-type" className="font16px" colSpan="2">{ship_type}</th>
          </tr>
          <tr>
            <th className="width_25">SHIP</th>
            <th className="width_25">LEVEL</th>
            <th className="width_25">RETROFIT</th>
            <th className="width_25">OATH</th>
          </tr>
          <tr>
            <th id="slot-ship-pick">
              <Pick defaultValue={ship_id} onChange={get_ship_option} azurapi={azurapi} />
            </th>
            <th id="slot-ship-level">
              <Level defaultValue={ship_level} onChange={get_level_option} />
            </th>
            <th id="slot-ship-retrofit">
              <Retrofit retrofit={ship_kai} onChange={get_retrofit_option} />
            </th>
            <th id="slot-ship-oath">
              <Oath defaultValue={ship_oath} onChange={get_oath_option} />
            </th>
          </tr>
        </thead>
      </Table>
    );
  };
};