// (((damage * ordnanceCount * 0.8) * (coefficient / 100)) / fixedReload).toFixed(2); // tbomb
// preload = (parseFloat(light) + ((fixedDamage * (80 / 100)) / 60)).toFixed(2); // tsurf
import Modifier from "./Modifier.js";

export default function DPS(equip, armor_modifier, type, coef="max", preload=false) {
  if ( type === "DD" || type === "CL" || type === "CA" || type === "CB" || type === "BB" ) {
    const damage            = parseFloat(equip.damage.max);
    const volley_multiplier = parseFloat(equip.volley.multiplier);
    const volley_count      = parseFloat(equip.volley.count);
    const rate_of_fire      = parseFloat(equip.rateOfFire.max.replace("s",""));
    const volley_time       = parseFloat(equip.volleyTime.value);
    const modifier          = Modifier(equip, type);

    let modifier_type       = 100;
    if ( armor_modifier === "Light"  ) { modifier_type = modifier.light };
    if ( armor_modifier === "Medium" ) { modifier_type = modifier.medium };
    if ( armor_modifier === "Heavy"  ) { modifier_type = modifier.heavy }; 

    let absolute_cooldown   = 0;
    if ( type === "DD" ) { absolute_cooldown = 0.26 };
    if ( type === "CL" ) { absolute_cooldown = 0.28 };
    if ( type === "CA" || type === "CB" ) { absolute_cooldown = 0.30 };
    if ( type === "BB" ) { absolute_cooldown = 0 };

    let coefficient         = equip.coefficient;
    if ( coefficient.value ) { coefficient = parseFloat(equip.coefficient.value) };
    if ( coefficient.max )   { coefficient = parseFloat(equip.coefficient.max.replace("%","")) };
    if ( coef === "min" )    { coefficient = parseFloat(equip.coefficient.min.replace("%","")) };

    const absolute_reload = ( rate_of_fire + volley_time + absolute_cooldown );
    const formula = (( damage * (coefficient / 100) * ( volley_multiplier * volley_count ) * (modifier_type / 100 )) / absolute_reload );

    return formula.toFixed(2);
  };

  if ( type === "TRP" || type === "STRP" ) {
    const damage         = parseFloat(equip.damage.max);
    const count          = parseFloat(equip.noOfTorpedoes.value);
    const rate_of_fire   = parseFloat(equip.rateOfFire.max.replace("s",""));
    const modifier       = Modifier(equip, type);
    let modifier_type    = 100;
    if ( armor_modifier === "Light"  ) { modifier_type = modifier.light };
    if ( armor_modifier === "Medium" ) { modifier_type = modifier.medium };
    if ( armor_modifier === "Heavy"  ) { modifier_type = modifier.heavy };

    const formula = (( damage * count * ( modifier_type / 100 ) ) / rate_of_fire );
    const preload_torpedo = ( formula + ( ( damage * count * ( modifier_type / 100 ) ) / 60 ));

    if ( preload  ) { return preload_torpedo.toFixed(2) }; 
    if ( !preload ) { return formula.toFixed(2) };
  };

  if ( type === "F" ) {
    // soon here after used on top
  };
};