import React, { Component } from "react";
import { Table } from "reactstrap";

import ImgR from "../../../components/ImgR.js";

export default class FleetSlot extends Component {
  render() {
    const azurapi   = this.props.azurapi;
    const banner    = this.props.banner;
    const modalData = this.props.modalData;

    const openShipModal = (type) => this.props.openShipModal({ slot: type });

    const openShipModal_1 = () => openShipModal(1);
    const openShipModal_2 = () => openShipModal(2);
    const openShipModal_3 = () => openShipModal(3);

    const extract = (idx, kai) => {
      if ( kai ) return idx + " (Retrofit)";
      return idx;
    };

    // slot1
    // const slot1_id     = modalData.slot1.id;
    const slot1_name   = extract(modalData.slot1.name, modalData.slot1.kai.status);
    const slot1_banner = modalData.slot1.banner;
    // const slot1_type   = modalData.slot1.type;
    // const slot1_kai    = modalData.slot1.kai;
    // const slot1_level  = modalData.slot1.level;
    // const slot1_oath   = modalData.slot1.oath;
    // const slot1_rarity = modalData.slot1.rarity;

    // slot2
    // const slot2_id     = modalData.slot3.id;
    const slot2_name   = extract(modalData.slot3.name, modalData.slot3.kai.status);
    const slot2_banner = modalData.slot2.banner;
    // const slot2_type   = modalData.slot2.type;
    // const slot2_kai    = modalData.slot2.kai;
    // const slot2_level  = modalData.slot2.level;
    // const slot2_oath   = modalData.slot2.oath;
    // const slot2_rarity = modalData.slot2.rarity;

    // slot3
    // const slot3_id     = modalData.slot3.id;
    const slot3_name   = extract(modalData.slot3.name, modalData.slot3.kai.status);
    const slot3_banner = modalData.slot3.banner;
    // const slot3_type   = modalData.slot3.type;
    // const slot3_kai    = modalData.slot3.kai;
    // const slot3_level  = modalData.slot3.level;
    // const slot3_oath   = modalData.slot3.oath;
    // const slot3_rarity = modalData.slot3.rarity;

    return (
      <>
        <Table>
          <thead>
            <tr>
              <th onClick={openShipModal_1}><ImgR src={slot1_banner} /></th>
              <th onClick={openShipModal_2}><ImgR src={slot2_banner} /></th>
              <th onClick={openShipModal_3}><ImgR src={slot3_banner} /></th>
            </tr>
            <tr>
              <th onClick={openShipModal_1}>{slot1_name}</th>
              <th onClick={openShipModal_2}>{slot2_name}</th>
              <th onClick={openShipModal_3}>{slot3_name}</th>
            </tr>
          </thead>
        </Table>
      </>
    );
  };
};