import React, { Component } from "react";
import { Table } from "reactstrap";

export default class Animation extends Component {
  render() {
    const animation_gif   = this.props.animation;
    if ( animation_gif !== "" ) {
      return (
        <Table>
          <thead>
            <tr>
              <th>Pattern Animation</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <img src={animation_gif} referrerPolicy="no-referrer" alt="BARRAGE" className="img-fluid" />
              </td>
            </tr>
          </tbody>
        </Table>
      );
    };
    return null
  }
}