import React, { Component } from "react";
import { Table } from "reactstrap";

import typeAE  from "../../../assets/AL/AE.png";
import typeAR  from "../../../assets/AL/AR.png";
import typeBB  from "../../../assets/AL/BB.png";
import typeBBV from "../../../assets/AL/BBV.png";
import typeBC  from "../../../assets/AL/BC.png";
import typeBM  from "../../../assets/AL/BM.png";
import typeCA  from "../../../assets/AL/CA.png";
import typeCB  from "../../../assets/AL/CB.png";
import typeCL  from "../../../assets/AL/CL.png";
import typeCV  from "../../../assets/AL/CV.png";
import typeCVL from "../../../assets/AL/CVL.png";
import typeDD  from "../../../assets/AL/DD.png";
import typeDDG from "../../../assets/AL/DDG.png";
import typeSS  from "../../../assets/AL/SS.png";
import typeSSV from "../../../assets/AL/SSV.png";

import Health    from "../../../assets/AL/Health.png";
import Firepower from "../../../assets/AL/Firepower.png";
import Torpedo   from "../../../assets/AL/Torpedo.png";
import AntiAir   from "../../../assets/AL/AntiAir.png";
import Reload    from "../../../assets/AL/Reload.png";
import Accuracy  from "../../../assets/AL/Hit.png";
import Evasion   from "../../../assets/AL/Evasion.png";
import ASW       from "../../../assets/AL/ASW.png";

import TechPoint from "../../../assets/AL/TechPoint.png";

export default class ShipTech extends Component {
  render() {
    const fleetTech = this.props.fleetTech;

    // statsBonus child => null || {applicable(multiple array[]), bonus, stat} (type: string or null) 
    const collection       = fleetTech.statsBonus.collection;
    const maxLevel         = fleetTech.statsBonus.maxLevel;
    // techPoints child => {collection, maxLimitBreak, maxLevel, total} (type: number ? null = 0)
    const techPoints       = fleetTech.techPoints;

    let collectionFormat = null;
    let maxLevelFormat = null;

    if(collection) {
      const applicable = collection.applicable;
      const bonus      = collection.bonus;
      const stat       = collection.stat;        
      let statType = null;
      if(stat === "health"              ) {statType = Health   }
      if(stat === "firepower"           ) {statType = Firepower}
      if(stat === "torpedo"             ) {statType = Torpedo  }
      if(stat === "antiair"             ) {statType = AntiAir  }
      if(stat === "reload"              ) {statType = Reload   }
      if(stat === "accuracy"            ) {statType = Accuracy }
      if(stat === "evasion"             ) {statType = Evasion  }
      if(stat === "antisubmarineWarfare") {statType = ASW      }
      const statFormat = (<img src={statType} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);

      collectionFormat = (
        <>
          {applicable.map((type, key) => {
            let hullType = null;
            if(type === "Battleship"              ) {hullType = typeBB };
            if(type === "Battlecruiser"           ) {hullType = typeBC };
            if(type === "Aviation battleship"     ) {hullType = typeBBV};
            if(type === "Destroyer"               ) {hullType = typeDD };
            if(type === "Guided-missile destroyer") {hullType = typeDDG};
            if(type === "Heavy cruiser"           ) {hullType = typeCA };
            if(type === "Large cruiser"           ) {hullType = typeCB };
            if(type === "light cruiser"           ) {hullType = typeCL };
            if(type === "Monitor"                 ) {hullType = typeBM };
            if(type === "Aircraft carrier"        ) {hullType = typeCV };
            if(type === "Light aircraft carrier"  ) {hullType = typeCVL};
            if(type === "Submarine"               ) {hullType = typeSS };
            if(type === "Submarine carrier"       ) {hullType = typeSSV};
            if(type === "Munition ship"           ) {hullType = typeAE };
            if(type === "Repair ship"             ) {hullType = typeAR };
            return (<img key={key} src={hullType} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);
          })}
          {statFormat}
          {bonus}
        </>
      );
    };
    if(maxLevel) {
      const applicable = maxLevel.applicable;
      const bonus      = maxLevel.bonus;
      const stat       = maxLevel.stat;
      let statType = null;
      if(stat === "health"              ) {statType = Health   }
      if(stat === "firepower"           ) {statType = Firepower}
      if(stat === "torpedo"             ) {statType = Torpedo  }
      if(stat === "antiair"             ) {statType = AntiAir  }
      if(stat === "reload"              ) {statType = Reload   }
      if(stat === "accuracy"            ) {statType = Accuracy }
      if(stat === "evasion"             ) {statType = Evasion  }
      if(stat === "antisubmarineWarfare") {statType = ASW      }
      const statFormat = (<img src={statType} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);
      maxLevelFormat   = (
        <>
          {applicable.map((type, key) => {
            let hullType = null;
              if(type === "Battleship"              ) {hullType = typeBB };
              if(type === "Battlecruiser"           ) {hullType = typeBC };
              if(type === "Aviation battleship"     ) {hullType = typeBBV};
              if(type === "Destroyer"               ) {hullType = typeDD };
              if(type === "Guided-missile destroyer") {hullType = typeDDG};
              if(type === "Heavy cruiser"           ) {hullType = typeCA };
              if(type === "Large cruiser"           ) {hullType = typeCB };
              if(type === "Light cruiser"           ) {hullType = typeCL };
              if(type === "Monitor"                 ) {hullType = typeBM };
              if(type === "Aircraft carrier"        ) {hullType = typeCV };
              if(type === "Light aircraft carrier"  ) {hullType = typeCVL};
              if(type === "Submarine"               ) {hullType = typeSS };
              if(type === "Submarine carrier"       ) {hullType = typeSSV};
              if(type === "Munition ship"           ) {hullType = typeAE };
              if(type === "Repair ship"             ) {hullType = typeAR };
              return (<img key={key} src={hullType} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);
          })}
          {statFormat}
          {bonus}
        </>
      );
    };

    const techPointsCollection    = techPoints.collection;
    const techPointsMaxLimitBreak = techPoints.maxLimitBreak;
    const techPointsMaxLevel      = techPoints.maxLevel;
    const techPointsTotal         = techPoints.total; 
    return (
      <div>
        <Table className="tableCS">
          <thead>
            <tr>
              <th colSpan="3">Fleet Technology</th>
            </tr>
            <tr>
              <th>Unlock Conditions</th>
              <th>Stat Bonuses</th>
              <th>Tech Points</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>Collection</th>
              <th>{collectionFormat}</th>
              <th>
                {techPointsCollection}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
            <tr>
              <th>Max Limit Break</th>
              <th></th>
              <th>
                {techPointsMaxLimitBreak}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
            <tr>
              <th>Level 120</th>
              <th>{maxLevelFormat}</th>
              <th>
                {techPointsMaxLevel}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
            <tr>
              <th colSpan="2">Total</th>
              <th>
                {techPointsTotal}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
          </tbody>
        </Table>
      </div>
    )
  }
}