import React, { Component } from "react";
import { Input } from "reactstrap";

export default class Level extends Component {
  render() {
    const default_value = this.props.defaultValue;
    const change        = this.props.onChange;

    return (
      <Input
        type="select"
        name="level-select"
        id="level-select"
        className="input_max"
        defaultValue={default_value}
        onChange={change}
      >
        <option id="level-select-125" value="125">Level 125</option>
        <option id="level-select-120" value="120">Level 120</option>
        <option id="level-select-100" value="100">Level 100</option>
      </Input>
    );
  };
};