import React, { Component } from "react";
import { Link } from "react-router-dom";
import { BreadcrumbItem } from "reactstrap";

// icon load
import "../../../assets/css/nucleo-icons.css";

export default class EXTEND_HOME extends Component {
  render() {
    return (
      <BreadcrumbItem>
        <Link to={`/home`}>
          <i className="tim-icons tim-icons-lg icon-atom" />
        </Link>
      </BreadcrumbItem>
    );
  };
};