import React, { Component } from "react";
import { Input } from "reactstrap";

export default class Reload extends Component {
  render() {
    const input_reload = this.props.input_reload;
    const input_tech   = this.props.input_tech;
    const input_cat    = this.props.input_cat;
    const ship_reload  = this.props.ship_reload;
    const ship_tech    = this.props.ship_tech;
    const ship_cat     = this.props.ship_cat;
    const final_reload = this.props.final_reload;

    return (
      <thead>
        <tr>
          <th>BASE RELOAD</th>
          <th>FLEET TECH RELOAD</th>
          <th>MEOWFFICER RELOAD</th>
          <th>FINAL RELOAD</th>
        </tr>
        <tr>
          <th>
            <Input
              id="reload-base"
              type="number" min="0"
              className="fleet-input"
              onChange={input_reload}
              defaultValue={ship_reload}
            />
          </th>
          <th>
            <Input
              id="reload-tech"
              type="number" min="0"
              className="fleet-input"
              onChange={input_tech}
              defaultValue={ship_tech}
            />
          </th>
          <th>
            <Input
              id="reload-cat"
              type="number" min="0"
              className="fleet-input"
              onChange={input_cat}
              defaultValue={ship_cat}
            />
          </th>
          <th>
            <Input
              id="reload-final"
              type="number" min="0"
              className="fleet-input-disabled"
              defaultValue={final_reload}
              disabled
            />
          </th>
        </tr>
      </thead>
    );
  };
};