import React, { Component } from "react";

// image
import Empty from "../../../../assets/AL/Empty.png";

// custom components
import ImgR from "../../../../components/ImgR.js";

export default class Equipment extends Component {
  render() {
    const data_build    = this.props.data_build;
    const gear          = this.props.gear;
    const gearData      = this.props.gearData;
    const gearSlot      = this.props.gearSlot;

    const openGearModal = (type) => this.props.openGearModal({ slot: type });

    const openGearModal_1 = () => openGearModal(1);
    const openGearModal_2 = () => openGearModal(2);
    const openGearModal_3 = () => openGearModal(3);
    const openGearModal_4 = () => openGearModal(4);
    const openGearModal_5 = () => openGearModal(5);

    let equipment_type_1 = "Primary";
    let equipment_type_2 = "Secondary";
    let equipment_type_3 = "Tertiary";
    let equipment_type_4 = "Auxliary";
    let equipment_type_5 = "Auxliary";

    let equipment_max_1 = 1;
    let equipment_max_2 = 1;
    let equipment_max_3 = 1;

    let equipment_efficiency_1 = 100;
    let equipment_efficiency_2 = 100;
    let equipment_efficiency_3 = 100;

    return (
      <>
        <thead>
          <tr>
            <th className="width-10">ICON</th>
            <th className="width-30">EQUIPMENT</th>
            <th className="width-12">MOUNT</th>
            <th className="width-12">SLOT COEFFICIENT</th>
            <th className="width-12">GEAR COEFFICIENT</th>
            <th className="width-12">GEAR RELOAD</th>
            <th className="width-12">FINAL RELOAD</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td onClick={openGearModal_1}><div className="equipment-empty-slot m-auto"><ImgR id="equipment-icon-1" src={Empty} /></div></td>
            <td id="equipment-type-1">{equipment_type_1}</td>
            <td id="equipment-max-1">{equipment_max_1}</td>
            <td id="equipment-efficiency-1">{equipment_efficiency_1}%</td>
            <td>0%</td>
            <td>00.00s</td>
            <td>00.00s</td>
          </tr>
          <tr>
            <td onClick={openGearModal_2}><div className="equipment-empty-slot m-auto"><ImgR id="equipment-icon-2" src={Empty} /></div></td>
            <td id="equipment-type-2">{equipment_type_2}</td>
            <td id="equipment-max-2">{equipment_max_2}</td>
            <td id="equipment-efficiency-2">{equipment_efficiency_2}%</td>
            <td>0%</td>
            <td>00.00s</td>
            <td>00.00s</td>
          </tr>
          <tr>
            <td onClick={openGearModal_3}><div className="equipment-empty-slot m-auto"><ImgR id="equipment-icon-3" src={Empty} /></div></td>
            <td id="equipment-type-3">{equipment_type_3}</td>
            <td id="equipment-max-3">{equipment_max_3}</td>
            <td id="equipment-efficiency-3">{equipment_efficiency_3}%</td>
            <td>0%</td>
            <td>00.00s</td>
            <td>00.00s</td>
          </tr>
          <tr>
            <td onClick={openGearModal_4}><div className="equipment-empty-slot m-auto"><ImgR id="equipment-icon-4" src={Empty} /></div></td>
            <td id="equipment-type-4">{equipment_type_4}</td>
            <td id="equipment-aux-1" colSpan="4">Description</td>
          </tr>
          <tr>
            <td onClick={openGearModal_5}><div className="equipment-empty-slot m-auto"><ImgR id="equipment-icon-5" src={Empty} /></div></td>
            <td id="equipment-type-5">{equipment_type_5}</td>
            <td id="equipment-aux-2" colSpan="4">Description</td>
          </tr>
        </tbody>
      </>
    );
  };
};