import React, { Component } from "react";

// Core Components
import Navbar from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer from "../../components/Footer/Footer.js";

import FleetSlot from "./FleetBuild/FleetSlot.js";
import FleetModal from "./FleetBuild/FleetModal.js";
import EquipmentModal from "./FleetBuild/EquipmentModal.js";

// function
import ShipGet from "../../components/ShipGet.js";

export default class FleetBuild extends Component {
  render() {
    const azurapi = ShipGet(this.props.azurAPI);
    const banner  = this.props.shipBanner;
    const stats   = this.props.stats;   
    const gear    = this.props.gear;
    
    const openShipModal = this.props.openShipModal;
    const openGearModal = this.props.openGearModal;

    const modalData  = this.props.modalData;
    const modalOpen  = this.props.modalOpen;
    const modalClose = this.props.modalClose;
    const slot       = this.props.slot;

    const gearData   = this.props.gearData;
    const gearOpen   = this.props.gearOpen;
    const gearClose  = this.props.gearClose;
    const gearSlot   = this.props.gearSlot;
    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle shipheader">
            <div className="content">
              <h1>Fleet</h1>
              <div>
                <FleetSlot
                  azurapi={azurapi}
                  banner={banner}
                  modalData={modalData}
                  openShipModal={openShipModal}
                />
                <FleetModal
                  azurapi={azurapi}
                  banner={banner}
                  stats={stats}
                  gear={gear}
                  modalData={modalData}
                  modalOpen={modalOpen}
                  modalClose={modalClose}
                  slot={slot}
                  openGearModal={openGearModal}
                  gearData={gearData}
                  gearSlot={gearSlot}
                />
                <EquipmentModal
                  gear={gear}
                  gearData={gearData}
                  gearOpen={gearOpen}
                  gearClose={gearClose}
                  gearSlot={gearSlot}
                />
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};