import React, { Component } from "react";

import ShipProfile from "./ShipProfile.js";
import ShipFetch from "./ShipFetch.js";

export default class ShipRoute extends Component {
  render(){
  	const shipData   = this.props.shipData;
    const bannerData = this.props.bannerData;
    const paramsId   = this.props.paramsId;
  	if(shipData === undefined) {
  	  return <ShipFetch paramsId={paramsId} />;
  	} else {
      return <ShipProfile shipData={shipData} bannerData={bannerData} />
  	}
  }
}