import React, { Component } from "react";
import { Input } from "reactstrap";

export default class Retrofit extends Component {
  render() {
    const status       = this.props.retrofit.status;
    const availability = this.props.retrofit.availability;
    const change       = this.props.onChange;
    let default_value  = 0;
    if ( status ) default_value = 1;
    if ( availability )
    {
      return (
        <Input
          type="select"
          name="retrofit-select"
          id="retrofit-select"
          className="input_max"
          defaultValue={default_value}
          onChange={change}
        >
          <option id="retrofit-select-false" value="0">FALSE</option>
          <option id="retrofit-select-true" value="1">TRUE</option>
        </Input>
      );
    }
    else
    {
      return (
        <Input
          type="select"
          name="retrofit-select"
          id="retrofit-select"
          className="input_max input_disabled"
          defaultValue={default_value}
          onChange={change}
          disabled
        >
          <option id="retrofit-select-false" value="0">FALSE</option>
          <option id="retrofit-select-true" value="1">TRUE</option>
        </Input>
      );
    };
  };
};