import React, { Component } from "react";
import { Col, NavLink, Card, CardText } from "reactstrap";

export default class IndexCard extends Component {
  render() {
    const Link        = this.props.tag;
    const text_link   = this.props.to;
    const text_title  = this.props.title;
    return (
      <Col xs="6">
        <div>
          <NavLink tag={Link} to={text_link}>
            <Card body outline color="primary">
              <CardText className="font-weight-bold">{text_title}</CardText>
            </Card>
          </NavLink>
        </div>
      </Col>
    );
  };
};