import React, { Component } from "react";
import { Table } from "reactstrap";

export default class Obtained extends Component {
  render() {
    const obtained_from = this.props.obtainedFrom;
    if ( obtained_from !== "" ) {
      return (
        <Table>
          <thead>
            <tr>
              <th>Obtained From</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{obtained_from}</td>
            </tr>
          </tbody>
        </Table>
      );
    };
    return null
  }
}