import React, { useState, useEffect } from "react";
import { Route, Routes, useLocation } from "react-router-dom";

// extend Components
import ProgressLoad from "../components/ProgressLoad.js";

// Core Components
import FleetBuild from "./Fleet/FleetBuild.js";

function Fleet() {
  const { pathname }            = useLocation();
  const [error, setError]       = useState(null);

  // azurapi state
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurAPI, setAzurAPI]   = useState([]);

  // shipbanner state
  const [bannerIsLoaded, setBannerIsLoaded] = useState(false);
  const [shipBanner, setShipBanner]         = useState([]);

  // stats state
  const [statsIsLoaded, setStatsIsLoaded]   = useState(false);
  const [stats, setStats]                   = useState([]);

  // gear state
  const [gearIsLoaded, setGearIsLoaded]     = useState(false);
  const [gear, setGear]                     = useState([]);

  useEffect(() => {
    if (pathname) window.scrollTo(0, 0);
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
    .then(res => res.json())
    .then(
      (result) => {
        setIsLoaded(true);
        setAzurAPI(result);
      },
      (error) => {
        setIsLoaded(true);
        setError(error);
      }
    );
    fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
    .then(res => res.json())
    .then(
      (result) => {
        setBannerIsLoaded(true);
        setShipBanner(result);
      },
      (error) => {
        setBannerIsLoaded(true);
        setError(error);
      }
    );
    fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/stats.json")
    .then(res => res.json())
    .then(
      (result) => {
        setStatsIsLoaded(true);
        setStats(result);
      },
      (error) => {
        setStatsIsLoaded(true);
        setError(error);
      }
    );
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/equipments.json")
    .then(res => res.json())
    .then(
      (result) => {
        setGearIsLoaded(true);
        setGear(result);
      },
      (error) => {
        setGearIsLoaded(true);
        setError(error);
      }
    )
  }, [pathname]);

  // slot state
  const [ slot, setSlot ] = useState({ slot: 0 });

  // main modal
  const [ modal, setModal ] = useState(false);
  const [ modalData, setModalData ] = useState({
    slot1: {
      azurapi: null,
      id: "000",
      name: "Unknown",
      banner: "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png",
      type: "Unknown-Type",
      kai: { status: false, availability: false },
      level: "125",
      oath: false,
      rarity: "Unknown-Rarity",
      reload: 0,
      tech: 0,
      cat: 0
    },
    slot2: {
      azurapi: null,
      id: "000",
      name: "Unknown",
      banner: "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png",
      type: "Unknown-Type",
      kai: { status: false, availability: false },
      level: "125",
      oath: false,
      rarity: "Unknown-Rarity",
      reload: 0,
      tech: 0,
      cat: 0
    },
    slot3: {
      azurapi: null,
      id: "000",
      name: "Unknown",
      banner: "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png",
      type: "Unknown-Type",
      kai: { status: false, availability: false },
      level: "125",
      oath: false,
      rarity: "Unknown-Rarity",
      reload: 0,
      tech: 0,
      cat: 0
    }
  });

  const openShipModal = (idx) => {
    setSlot({ slot: idx.slot });
    setModal(true);
  };

  const closeModal = (idx) => {
    let slots = idx.slot;
    let slot1 = modalData.slot1;
    let slot2 = modalData.slot2;
    let slot3 = modalData.slot3;

    if ( slots === 1 ) slot1 = idx;
    if ( slots === 2 ) slot2 = idx;
    if ( slots === 3 ) slot3 = idx;

    setModalData({
      slot1: {
        azurapi: slot1.azurapi,
        id: slot1.id,
        name: slot1.name,
        banner: slot1.banner,
        type: slot1.type,
        kai: slot1.kai,
        level: slot1.level,
        oath: slot1.oath,
        rarity: slot1.rarity,
        reload: parseInt(slot1.reload),
        tech: parseInt(slot1.tech),
        cat: parseInt(slot1.cat)
      },
      slot2: {
        azurapi: slot2.azurapi,
        id: slot2.id,
        name: slot2.name,
        banner: slot2.banner,
        type: slot2.type,
        kai: slot2.kai,
        level: slot2.level,
        oath: slot2.oath,
        rarity: slot2.rarity,
        reload: parseInt(slot2.reload),
        tech: parseInt(slot2.tech),
        cat: parseInt(slot2.cat)
      },
      slot3: {
        azurapi: slot3.azurapi,
        id: slot3.id,
        name: slot3.name,
        banner: slot3.banner,
        type: slot3.type,
        kai: slot3.kai,
        level: slot3.level,
        oath: slot3.oath,
        rarity: slot3.rarity,
        reload: parseInt(slot3.reload),
        tech: parseInt(slot3.tech),
        cat: parseInt(slot3.cat)
      }
    });
    setModal(false);
  };

  // equipment modal
  const [ gearSlot, setGearSlot ]   = useState({ slot: 0 });
  const [ gearModal, setGearModal ] = useState(false);
  const [ gearData, setGearData ]   = useState({
    slot1: {
      primary: "000",
      secondary: "000",
      tertiary: "000",
      auxiliary_1: "000",
      auxiliary_2: "000"
    },
    slot2: {
      primary: "000",
      secondary: "000",
      tertiary: "000",
      auxiliary_1: "000",
      auxiliary_2: "000"
    },
    slot3: {
      primary: "000",
      secondary: "000",
      tertiary: "000",
      auxiliary_1: "000",
      auxiliary_2: "000"
    },
  });
  const openGearModal = (idx) => {
    setGearSlot({ slot: idx.slot });
    setGearModal(true);
  };

  const gearClose = (idx) => {
    let slots = idx.slot;
    let slot1 = gearData.slot1;
    let slot2 = gearData.slot2;
    let slot3 = gearData.slot3;

    if ( slots === 1 ) slot1 = idx;
    if ( slots === 2 ) slot2 = idx;
    if ( slots === 3 ) slot3 = idx;

    setGearData({
      slot1: {
        primary: slot1.primary,
        secondary: slot1.secondary,
        tertiary: slot1.tertiary,
        auxiliary_1: slot1.auxiliary_1,
        auxiliary_2: slot1.auxiliary_2
      },
      slot2: {
        primary: slot2.primary,
        secondary: slot2.secondary,
        tertiary: slot2.tertiary,
        auxiliary_1: slot2.auxiliary_1,
        auxiliary_2: slot2.auxiliary_2
      },
      slot3: {
        primary: slot3.primary,
        secondary: slot3.secondary,
        tertiary: slot3.tertiary,
        auxiliary_1: slot3.auxiliary_1,
        auxiliary_2: slot3.auxiliary_2
      },
    });
    setGearModal(false);
  };

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <ProgressLoad text="Loading AzurAPI..." />
  } else if (!bannerIsLoaded) {
    return <ProgressLoad text="Loading ShipBanner..." />
  } else if (!statsIsLoaded) {
    return <ProgressLoad text="Loading Stats..." />
  } else if (!gearIsLoaded) {
    return <ProgressLoad text="Loading Equipments..." />
  } else {
    return (
      <>
        <Routes>
          <Route 
            path="" 
            element={
              <FleetBuild 
                azurAPI={azurAPI} 
                shipBanner={shipBanner}
                stats={stats}
                gear={gear}
                openShipModal={openShipModal}
                modalOpen={modal}
                modalClose={closeModal}
                modalData={modalData}
                openGearModal={openGearModal}
                slot={slot.slot}
                gearData={gearData}
                gearOpen={gearModal}
                gearClose={gearClose}
                gearSlot={gearSlot.slot}
              />
            } 
          />
        </Routes>
      </>
    );
  }
}

export default Fleet;
