export default function Raw(equip, type, coef="max") {
  const damage            = parseFloat(equip.damage.max);
  const volley_multiplier = parseFloat(equip.volley.multiplier);
  const volley_count      = parseFloat(equip.volley.count);
  const rate_of_fire      = parseFloat(equip.rateOfFire.max.replace("s",""));
  const volley_time       = parseFloat(equip.volleyTime.value);

  let coefficient         = equip.coefficient;
  if ( coefficient.value ) { coefficient = parseFloat(equip.coefficient.value) };
  if ( coefficient.max )   { coefficient = parseFloat(equip.coefficient.max.replace("%","")) };
  if ( coef === "min" )    { coefficient = parseFloat(equip.coefficient.min.replace("%","")) };

  let absolute_cooldown   = 0;
  if ( type === "DD" ) { absolute_cooldown = 0.26 };
  if ( type === "CL" ) { absolute_cooldown = 0.28 };
  if ( type === "CA" || type === "CB" ) { absolute_cooldown = 0.30 };
  if ( type === "BB" ) { absolute_cooldown = 0 };



  return ((( damage * ( coefficient / 100 ) ) * ( volley_multiplier * volley_count ) * 1 ) / ( rate_of_fire + volley_time + absolute_cooldown )).toFixed(2);
}