import React, { Component } from "react";

// Components
import ImgR from "../../../../../components/ImgR.js";

// Functions
import FactionClass from "../../../../../components/FactionClass.js";
import RarityGet from "../../../../../components/RarityGet.js";
import DPS from "./Formula/DPS.js";

export default class Torpedo extends Component {
  render() {
    const navigate       = this.props.navigate;
    const equipment_list = this.props.list;
    const gear_type      = "TRP";

    return (
      <tbody>
        {equipment_list.map((equip, key) => {
          const icon             = equip.image;
          const id               = equip.id;
          const faction          = equip.nationality;
          const faction_class    = FactionClass(faction);
          const equipment_rarity = equip.highest_rarity;
          const equipment_link   = encodeURIComponent(id.replace(/\s+/g, "_"));
          const highest_rarity   = RarityGet(equipment_rarity);
          let tier               = equip.tiers[0];

          for ( var i = 0; i < equip.tiers.length; i++ ) {
            if ( equip.tiers[i] && equip.tiers[i].rarity === highest_rarity ) { tier = equip.tiers[i].stats };
          };

          let torpedo   = 0;
          let spread    = 0;
          let range     = 0;

          // Torpedo
          if ( tier.torpedo ) { torpedo = tier.torpedo.formatted };

          // Spread
          if ( tier.spread ) { spread = tier.spread.value + "" };
          if ( id === "Triple 152mm (Model 1934)" ) { spread = "18" };

          // Range
          if ( tier.range ) { range = tier.range.firing };

          const damage         = tier.damage.max;
          const noOfTorpedoes  = tier.noOfTorpedoes.value;
          const angle          = tier.angle.formatted;
          const rateOfFire     = tier.rateOfFire.max;
          const characteristic = tier.characteristic.formatted;

          const light          = DPS(tier, "Light", gear_type);
          const medium         = DPS(tier, "Medium", gear_type);
          const heavy          = DPS(tier, "Heavy", gear_type);
          const preload_light  = DPS(tier, "Light", gear_type, "max", true);
          const preload_medium = DPS(tier, "Medium", gear_type, "max", true);
          const preload_heavy  = DPS(tier, "Heavy", gear_type, "max", true);

          return (
            <tr key={key} onClick={() => navigate(equipment_link)}>
              <td className={"rarity-" + equipment_rarity}><ImgR src={icon} /></td>
              <td>{id}</td>
              <td>{torpedo}</td>
              <td>{noOfTorpedoes}</td>
              <td>{damage}</td>
              <td>{rateOfFire}</td>
              <td className="dps-l">{light}</td>
              <td className="dps-m">{medium}</td>
              <td className="dps-h">{heavy}</td>
              <td className="dps-pl">{preload_light}</td>
              <td className="dps-pm">{preload_medium}</td>
              <td className="dps-ph">{preload_heavy}</td>
              <td>{range}</td>
              <td>{spread}</td>
              <td>{angle}</td>
              <td>{characteristic}</td>
              <td className={faction_class + " faction-background"}>{faction}</td>
            </tr>
          )
        })}
      </tbody>
    )
  };
};