import React, { Component } from "react";
import { Button, Modal, ModalBody, Table } from "reactstrap";

// Stats Icon 
import statAntiAir     from "../../assets/AL/AntiAir.png";
import statArmor       from "../../assets/AL/Armor.png";
import statASW         from "../../assets/AL/ASW.png";
import statAviation    from "../../assets/AL/Aviation.png";
import statConsumption from "../../assets/AL/Consumption.png";
import statEvasion     from "../../assets/AL/Evasion.png";
import statFirepower   from "../../assets/AL/Firepower.png";
import statHealth      from "../../assets/AL/Health.png";
import statHit         from "../../assets/AL/Hit.png";
import statLuck        from "../../assets/AL/Luck.png";
import statReload      from "../../assets/AL/Reload.png";
import statTorpedo     from "../../assets/AL/Torpedo.png";

import ImgR from "../../components/ImgR";
import DatamineSkill from "./DatamineModal/DatamineSkill.js";

export default class DatamineModal extends Component {
  render() {
    const modalOpen  = this.props.modalOpen;
    const modalClose = this.props.modalClose;
    const modalData  = this.props.modalData;

    const ship_code       = modalData.code;
    const ship_name       = modalData.name;
    const ship_name_en    = modalData.name_en;
    const ship_type       = modalData.hullType;
    const ship_faction    = modalData.faction;
    const ship_rarity     = modalData.rarity;
    const ship_skill      = modalData.skill;

    const limitbreak_1    = modalData.limitbreak[0];
    const limitbreak_2    = modalData.limitbreak[1];
    const limitbreak_3    = modalData.limitbreak[2];
    const limitbreak_1_en = modalData.limitbreak_en[0];
    const limitbreak_2_en = modalData.limitbreak_en[1];
    const limitbreak_3_en = modalData.limitbreak_en[2];

    const equip_slot_1    = modalData.equip.slot1;
    const equip_slot_2    = modalData.equip.slot2;
    const equip_slot_3    = modalData.equip.slot3;

    const stats_health    = modalData.stats.health;
    const stats_armor     = modalData.stats.armor;
    const stats_firepower = modalData.stats.firepower;
    const stats_torpedo   = modalData.stats.torpedo;
    const stats_antiAir   = modalData.stats.antiAir;
    const stats_aviation  = modalData.stats.aviation;
    const stats_reload    = modalData.stats.reload;
    const stats_hit       = modalData.stats.hit;
    const stats_evasion   = modalData.stats.evasion;
    const stats_speed     = modalData.stats.speed;
    const stats_luck      = modalData.stats.luck;
    const stats_asw       = modalData.stats.asw;
    const stats_cost      = modalData.stats.cost;

    return (
      <Modal isOpen={modalOpen} toggle={modalClose} size="xl" className="datamineModal">
        <ModalBody>
          <h3 className="text-center catModalHeader">
            <strong>{ship_code}<br/>{ship_name}<br/><i>{ship_name_en}</i></strong>
          </h3>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th className="width_50">RARITY</th>
                <th className="width_50">{ship_rarity}</th>
              </tr>
              <tr>
                <th>TYPE</th>
                <th>{ship_type}</th>
              </tr>
              <tr>
                <th>FACTION</th>
                <th>{ship_faction}</th>
              </tr>
              <tr>
                <th>LEVEL</th>
                <th>125</th>
              </tr>
            </thead>
          </Table>
          <Table bordered className="catModalTable">
            <tbody>
              <tr>
                <th className="width_25"><ImgR src={statHealth} /></th>
                <th className="width_25">{stats_health}</th>
                <th className="width_25"><ImgR src={statArmor} /></th>
                <th className="width_25">{stats_armor}</th>
              </tr>
              <tr>
                <th><ImgR src={statFirepower} /></th>
                <th>{stats_firepower}</th>
                <th><ImgR src={statTorpedo} /></th>
                <th>{stats_torpedo}</th>
              </tr>
              <tr>
                <th><ImgR src={statAntiAir} /></th>
                <th>{stats_antiAir}</th>
                <th><ImgR src={statAviation} /></th>
                <th>{stats_aviation}</th>
              </tr>
              <tr>
                <th><ImgR src={statReload} /></th>
                <th>{stats_reload}</th>
                <th><ImgR src={statHit} /></th>
                <th>{stats_hit}</th>
              </tr>
              <tr>
                <th><ImgR src={statEvasion} /></th>
                <th>{stats_evasion}</th>
                <th>SPD</th>
                <th>{stats_speed}</th>
              </tr>
              <tr>
                <th><ImgR src={statLuck} /></th>
                <th>{stats_luck}</th>
                <th><ImgR src={statASW} /></th>
                <th>{stats_asw}</th>
              </tr>
              <tr>
                <th><ImgR src={statConsumption} /></th>
                <th>{stats_cost}</th>
                <th></th>
                <th></th>
              </tr>
            </tbody>
          </Table>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th>Limit Break</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{limitbreak_1}<br/><i>{limitbreak_1_en}</i></td>
              </tr>
              <tr>
                <td>{limitbreak_2}<br/><i>{limitbreak_2_en}</i></td>
              </tr>
              <tr>
                <td>{limitbreak_3}<br/><i>{limitbreak_3_en}</i></td>
              </tr>
            </tbody>
          </Table>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th>SLOT</th>
                <th>COEFFICIENT</th>
                <th>GUN MOUNT</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{equip_slot_1.type}</td>
                <td>{equip_slot_1.efficiency}</td>
                <td>{equip_slot_1.count}</td>
              </tr>
              <tr>
                <td>{equip_slot_2.type}</td>
                <td>{equip_slot_2.efficiency}</td>
                <td>{equip_slot_2.count}</td>
              </tr>
              <tr>
                <td>{equip_slot_3.type}</td>
                <td>{equip_slot_3.efficiency}</td>
                <td>{equip_slot_3.count}</td>
              </tr>
            </tbody>
          </Table>
          <Table bordered className="catModalTable catModalTableAlign">
            <thead>
              <tr>
                <th colSpan="2">Skill</th>
              </tr>
            </thead>
            {ship_skill.map((skill, key) => {
              return <DatamineSkill skill={skill} key={key} />
            })}
          </Table>
          <div className="mobile-view text-center align-middle">
            <Button color="danger" onClick={modalClose}>Close</Button>
          </div>
        </ModalBody>
      </Modal>
    );
  };
};