import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

// datamine components
import ProgressLoad from "../components/ProgressLoad.js";
import DatamineList from "./Datamine/DatamineList.js";
import DatamineModal from "./Datamine/DatamineModal.js";

function Datamine() {
  const { pathname }            = useLocation();
  const [error, setError]       = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [datamine, setDatamine] = useState([]);
  const [modal, setModal]       = useState(false);

  const [modalData, setModalData]    = useState({
    id: null,
    name: null,
    name_en: null,
    hullType: null,
    faction: null,
    rarity: null,
    limitbreak: [],
    limitbreak_en: [],
    equip: {
      slot1: {
        type: null,
        efficientcy: null,
        count: null
      },
      slot2: {
        type: null,
        efficientcy: null,
        count: null
      },
      slot3: {
        type: null,
        efficientcy: null,
        count: null
      }
    },
    stats: {
      health: null,
      armor: null,
      firepower: null,
      torpedo: null,
      antiAir: null,
      aviation: null,
      reload: null,
      hit: null,
      evasion: null,
      speed: null,
      luck: null,
      asw: null,
      cost: null
    },
    skill: []
  });

  const openModal = (data) => {
    setModalData(data);
    setModal(true);
  };

  const closeModal = () => {
    setModal(false);
  };
  useEffect(() => {
    if (pathname) window.scrollTo(0, 0);
    fetch("https://raw.githubusercontent.com/Fernando2603/AzurMine/main/main.json")
    .then(res => res.json())
    .then(
      (result) => {
        setIsLoaded(true);
        setDatamine(result);
      },
      (error) => {
        setIsLoaded(true);
        setError(error);
      }
    )
  }, [pathname]);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <ProgressLoad text="Loading Datamine..." />
  } else {
    return (
      <>
        <DatamineList datamine={datamine} openModal={openModal} />
        <DatamineModal modalOpen={modal} modalClose={closeModal} modalData={modalData} />
      </>
    );
  }
}

export default Datamine;
