import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Alert } from "reactstrap";

// Core Components
import Navbar from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer from "../../components/Footer/Footer.js";

import UnknownBanner from "../../assets/AL/UnknownBanner.png";
export default class ShipList extends Component {
  render() {
    const azurAPI    = this.props.azurAPI;
    const shipBanner = this.props.shipBanner;
    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <Alert color="warning" className="text-center align-middle">
            This page is under Development!
          </Alert>
          <div className="main text-center align-middle shipheader">
            <div className="content">
              <h1>Ship List</h1>
              <div className="containerCS">
                {azurAPI.map((ship) => {
                  const rarity      = ship.rarity;
                  const shipID      = ship.id;
                  let shipName      = ship.names.en;
                  if(shipID === "30001") {shipName = "Hiryuu META"};
                  if(shipID === "30002") {shipName = "Ark Royal META"};

                  const shipLink    = encodeURIComponent(shipName.replace(/\s+/g, "_"));
                  const bannerData  = shipBanner.find(banner => banner.id === shipID);

                  let bannerRarity  = null;
                  let bannerStatus  = true;
                  let defaultBanner = UnknownBanner;
                  if(bannerData) {defaultBanner = bannerData.skins[0].banner; bannerStatus = false;};
                  if(bannerStatus) {console.log(shipName); console.log(ship)};

                  if(rarity === "Normal")                              {bannerRarity = "rarity-1"};
                  if(rarity === "Rare")                                {bannerRarity = "rarity-2"};
                  if(rarity === "Elite")                               {bannerRarity = "rarity-3"};
                  if(rarity === "Super Rare" || rarity === "Priority") {bannerRarity = "rarity-4"};
                  if(rarity === "Ultra Rare" || rarity === "Decisive") {bannerRarity = "rarity-5"};
                  if(bannerRarity === null)                            {bannerRarity = "rarity-0"};

                  return (
                    <div className="cardCS shipCard" key={ship._gid}>
                      <Link to={`/ship/${shipLink}`} className="shipName linkCS">
                        <div className={bannerRarity + " bannerContainer"}>
                          <img src={defaultBanner} referrerPolicy="no-referrer" alt="..." className="img-fluid shipBanner" />
                        </div>
                        <br />
                        {shipName}
                      </Link >
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    )
  }
}