import React, { Component } from "react";
import { Alert } from "reactstrap";

// Core Components
import Navigator from "../../components/Navigator/Navigator.js";
import Navbar from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer from "../../components/Footer/Footer.js";

import Mobile from "./EquipmentType/Mobile.js";
import Desktop from "./EquipmentType/Desktop.js";

export default class EquipmentType extends Component {
  render() {
    let navigate          = this.props.navigate;
    const azurapi         = this.props.azurAPI;
    const match_type_list = this.props.type;

    let equipment_type    = "";
    if ( match_type_list === 'destroyer_guns' )           { equipment_type = 'Destroyer Guns' };
    if ( match_type_list === 'light_cruiser_guns' )       { equipment_type = 'Light Cruiser Guns' };
    if ( match_type_list === 'heavy_cruiser_guns' )       { equipment_type = 'Heavy Cruiser Guns' };
    if ( match_type_list === 'large_cruiser_guns' )       { equipment_type = 'Large Cruiser Guns' };
    if ( match_type_list === 'battleship_guns' )          { equipment_type = 'Battleship Guns' };
    if ( match_type_list === 'ship_torpedoes' )           { equipment_type = 'Ship Torpedoes' };
    if ( match_type_list === 'submarine_torpedoes' )      { equipment_type = 'Submarine Torpedoes' };
    if ( match_type_list === 'fighter_planes' )           { equipment_type = 'Fighter Planes' };
    if ( match_type_list === 'dive_bomber_planes' )       { equipment_type = 'Dive Bomber Planes' };
    if ( match_type_list === 'torpedo_bomber_planes' )    { equipment_type = 'Torpedo Bomber Planes' };
    if ( match_type_list === 'seaplanes' )                { equipment_type = 'Seaplanes' };
    if ( match_type_list === 'anti-air_guns' )            { equipment_type = 'Anti-Air Guns' };
    if ( match_type_list === 'anti-submarine_equipment' ) { equipment_type = 'Anti-Submarine Equipment' };

    let equipment_list    = [];
    azurapi.forEach((gear) => {
      if ( gear.category === equipment_type ) {
        let current_gear        = gear;
        let rarity_container    = [];
        gear.tiers.forEach((idx) => { if ( idx ) rarity_container.push(idx.rarity) });
        let equipment_rarity    = 0;
        rarity_container.forEach((x) => {
          if ( x === "Normal"     && equipment_rarity < 1 ) equipment_rarity = 1;
          if ( x === "Rare"       && equipment_rarity < 2 ) equipment_rarity = 2;
          if ( x === "Elite"      && equipment_rarity < 3 ) equipment_rarity = 3;
          if ( x === "Super Rare" && equipment_rarity < 4 ) equipment_rarity = 4;
          if ( x === "Ultra Rare" && equipment_rarity < 5 ) equipment_rarity = 5;
        });
        current_gear = { ...current_gear, "highest_rarity": equipment_rarity };
        if ( equipment_rarity > 1 ) { 
          equipment_list.push(current_gear);
        };
      };
    });
    equipment_list.sort((a,b) => { return b.highest_rarity - a.highest_rarity });

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle">
            <div className="content">
              <Navigator equipment path={equipment_type} />
              <Alert color="warning">
                This page is under Development!
              </Alert>
              <Mobile navigate={navigate} equipmentList={equipment_list} />
              <Desktop navigate={navigate} type={match_type_list} equipmentList={equipment_list} />
            </div>
          </div>
          <Footer />
        </div>
      </>
    )
  }
}