import React, { useState, useEffect } from "react";
import { Route, Routes, useLocation, useParams } from "react-router-dom";

// extend Components
import ProgressLoad from "../components/ProgressLoad.js";

// Ship Components
import ShipRoute from "../views/Ship/ShipRoute";
import ShipList from "../views/Ship/ShipList";

function Ship() {
  const { pathname }            = useLocation();
  const [error, setError]       = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurAPI, setAzurAPI]   = useState([]);

  const [bannerIsLoaded, setBannerIsLoaded] = useState(false);
  const [shipBanner, setShipBanner]         = useState([]);

  useEffect(() => {
    if (pathname) window.scrollTo(0, 0);
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
    .then(res => res.json())
    .then(
      (result) => {
        setIsLoaded(true);
        setAzurAPI(result);
      },
      (error) => {
        setIsLoaded(true);
        setError(error);
      }
    );
    fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
    .then(res => res.json())
    .then(
      (result) => {
        setBannerIsLoaded(true);
        setShipBanner(result);
      },
      (error) => {
        setBannerIsLoaded(true);
        setError(error);
      }
    )
  }, [pathname]);

  // this should make new class component but its in parent class so fine
  const ShipRouteMatch = () => {
    const { id }     = useParams();
    let id_azurapi   = id.toLowerCase();
    const id_banner  = id_azurapi;
    if(id_azurapi === "hiryuu_meta"   ) {id_azurapi = "hiryu.meta"};
    if(id_azurapi === "ark_royal_meta") {id_azurapi = "royal.meta"};

    const shipData   = azurAPI.find(ship => ship.names.en.toLowerCase().replace(/\s+/g, "_") === id_azurapi);
    const bannerData = shipBanner.find(banner => banner.name.toLowerCase().replace(/\s+/g, "_") === id_banner);
    return <ShipRoute shipData={shipData} bannerData={bannerData} paramsId={id} />
  };

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <ProgressLoad text="Loading AzurAPI..." />
  } else if (!bannerIsLoaded) {
    return <ProgressLoad text="Loading ShipBanner..." />
  } else {
    return (
      <>
        <Routes>
          <Route path=":id" element={<ShipRouteMatch />} />
          <Route path="" element={<ShipList azurAPI={azurAPI} shipBanner={shipBanner}/>} />
        </Routes>
      </>
    );
  }
}

export default Ship;
