export default function Ordnance(unit) {
  let light        = 0;
  let medium       = 0;
  let heavy        = 0;
  let splash_range = 0;
  let spread       = 0;
  let bomb_damage  = 0;

  if ( unit.includes("100lb Bomb") ) {
    light        = 80;
    medium       = 85;
    heavy        = 100;
    splash_range = 16;
    spread       = 33;
    if ( unit.includes("T2") ) { bomb_damage = 159 };
    if ( unit.includes("T3") ) { bomb_damage = 173 };
    if ( unit.includes("T0") ) { bomb_damage = 185 };
  };

  if ( unit.includes("500lb Bomb") ) {
    light        = 80;
    medium       = 90;
    heavy        = 110;
    splash_range = 19;
    spread       = 36;
    if ( unit.includes("T2") ) { bomb_damage = 330 };
    if ( unit.includes("T3") ) { bomb_damage = 360 };
    if ( unit.includes("T0") ) { bomb_damage = 380 };
  };

  if ( unit.includes("1000lb Bomb") ) {
    light        = 80;
    medium       = 95;
    heavy        = 115;
    splash_range = 22;
    spread       = 40;
    if ( unit.includes("T2") ) { bomb_damage = 369 };
    if ( unit.includes("T3") ) { bomb_damage = 402 };
    if ( unit.includes("T0") ) { bomb_damage = 416 };
  };

  if ( unit.includes("1000lb BC Bomb") ) {
    light        = 80;
    medium       = 95;
    heavy        = 115;
    splash_range = 22;
    spread       = 40;
    bomb_damage     = 440;
  };

  if ( unit.includes("1600lb Bomb") ) {
    light        = 75;
    medium       = 100;
    heavy        = 120;
    splash_range = 25;
    spread       = 40;
    if ( unit.includes("T2") ) { bomb_damage = 393 };
    if ( unit.includes("T3") ) { bomb_damage = 429 };
  };

  if ( unit.includes("1600lb AP Bomb") ) {
    light        = 80;
    medium       = 110;
    heavy        = 130;
    splash_range = 25;
    spread       = 40;
    bomb_damage     = 429;
  };

  if ( unit.includes("800kg AP Bomb") ) {
    light        = 80;
    medium       = 110;
    heavy        = 130;
    splash_range = 25;
    spread       = 40;
    bomb_damage     = 432;
  };

  if ( unit.includes("2000lb Bomb") ) {
    light        = 70;
    medium       = 105;
    heavy        = 125;
    splash_range = 28;
    spread       = 42;
    if ( unit.includes("T2") ) { bomb_damage = 417 };
    if ( unit.includes("T3") ) { bomb_damage = 456 };
    if ( unit.includes("T0") ) { bomb_damage = 480 };
  };

  if ( unit.includes("AP Rocket") ) {
    light        = 110;
    medium       = 140;
    heavy        = 50;
    splash_range = 7;
    spread       = "12 x 5";
    bomb_damage     = 160;
  };

  return ({
    light: light,
    medium: medium,
    heavy: heavy,
    splash_range: splash_range,
    spread: spread,
    bomb_damage: bomb_damage
  });
};