import React, { Component } from "react";

// Component
import Vanguard from "./TableBody/Vanguard.js";
import Main from "./TableBody/Main.js";
import Torpedo from "./TableBody/Torpedo.js";

// Function
import TypeGet from "./TypeGet.js";

export default class TableBody extends Component {
  render() {
    const navigate         = this.props.navigate;
    const equipment_list   = this.props.list;
    const equipment_type   = this.props.type;

    let equipment_template = TypeGet(equipment_type);

    if ( equipment_template === 0 ) { return null };
    if ( equipment_template === 1 ) { return <Vanguard navigate={navigate} list={equipment_list} type={equipment_type} /> };
    if ( equipment_template === 2 ) { return <Main navigate={navigate} list={equipment_list} type={equipment_type} /> };
    if ( equipment_template === 3 ) { return <Torpedo navigate={navigate} list={equipment_list} /> };
    if ( equipment_template === 4 ) { return null };
    if ( equipment_template === 5 ) { return null };
    if ( equipment_template === 6 ) { return null };
    if ( equipment_template === 7 ) { return null };
    if ( equipment_template === 8 ) { return null };
  };
};