import React, { Component } from "react";
import { Progress } from "reactstrap";

export default class ProgressLoad extends Component {
  render() {
    const text_filler = this.props.text;
    let valuetime     = Math.floor(Math.random() * 100);
    return (
      <div className="progress-container progress-danger azurapi-load">
        <span className="progress-badge">{text_filler}</span>
        <Progress max="100" value={valuetime} />
      </div>
    );
  };
};