import React, { Component } from "react";

// Components
import Accuracy from "./TableHead/Accuracy.js";
import Ammo from "./TableHead/Ammo.js";
import Angle from "./TableHead/Angle.js";
import AntiAir from "./TableHead/AntiAir.js";
import AntiAirBurst from "./TableHead/AntiAirBurst.js";
import AntiAirDPS from "./TableHead/AntiAirDPS.js";
import ASW from "./TableHead/ASW.js";
import Attribute from "./TableHead/Attribute.js";
import Aviation from "./TableHead/Aviation.js";
import Coefficient from "./TableHead/Coefficient.js";
import CrashDamage from "./TableHead/CrashDamage.js";
import Damage from "./TableHead/Damage.js";
import DPS from "./TableHead/DPS.js";
import Equipment from "./TableHead/Equipment.js";
import Firepower from "./TableHead/Firepower.js";
import Guns from "./TableHead/Guns.js";
import Heavy from "./TableHead/Heavy.js";
import Hit from "./TableHead/Hit.js";
import Icon from "./TableHead/Icon.js";
import Light from "./TableHead/Light.js";
import Medium from "./TableHead/Medium.js";
import Nation from "./TableHead/Nation.js";
import Notes from "./TableHead/Notes.js";
import Ordnance from "./TableHead/Ordnance.js";
import PreloadHeavy from "./TableHead/PreloadHeavy.js";
import PreloadLight from "./TableHead/PreloadLight.js";
import PreloadMedium from "./TableHead/PreloadMedium.js";
import Radius from "./TableHead/Radius.js";
import Range from "./TableHead/Range.js";
import Raw from "./TableHead/Raw.js";
import Reload from "./TableHead/Reload.js";
import Reloads from "./TableHead/Reloads.js";
import Round from "./TableHead/Round.js";
import Rounds from "./TableHead/Rounds.js";
import Speed from "./TableHead/Speed.js";
import Spread from "./TableHead/Spread.js";
import Stars from "./TableHead/Stars.js";
import Torpedo from "./TableHead/Torpedo.js";
import Type from "./TableHead/Type.js";
import VolleyTime from "./TableHead/VolleyTime.js";

// Function
import TypeGet from "./TypeGet.js";

export default class TableHead extends Component {
  render() {
    const equipment_type     = this.props.type;
    const equipment_template = TypeGet(equipment_type);

    if ( equipment_template === 0 ) { return null };

    if ( equipment_template === 1 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Firepower/>
            <AntiAir/>
            <Round/>
            <Damage/>
            <Coefficient/>
            <VolleyTime/>
            <Reload/>
            <Rounds/>
            <Raw/>
            <Light/>
            <Medium/>
            <Heavy/>
            <Range/>
            <Spread/>
            <Angle/>
            <Attribute/>
            <Ammo/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 2 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Firepower/>
            <AntiAir/>
            <Round/>
            <Damage/>
            <Coefficient/>
            <VolleyTime/>
            <Reload/>
            <Raw/>
            <Light/>
            <Medium/>
            <Heavy/>
            <Range/>
            <Spread/>
            <Angle/>
            <Attribute/>
            <Ammo/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 3 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Torpedo/>
            <Round/>
            <Damage/>
            <Reload/>
            <Light/>
            <Medium/>
            <Heavy/>
            <PreloadLight/>
            <PreloadMedium/>
            <PreloadHeavy/>
            <Range/>
            <Spread/>
            <Angle/>
            <Attribute/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 4 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Torpedo/>
            <Round/>
            <Damage/>
            <Reload/>
            <Light/>
            <Medium/>
            <Heavy/>
            <Range/>
            <Spread/>
            <Angle/>
            <Attribute/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 5 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Aviation/>
            <Ordnance/>
            <Guns/>
            <Reload/>
            <Light/>
            <Medium/>
            <Heavy/>
            <AntiAirDPS/>
            <AntiAirBurst/>
            <Speed/>
            <CrashDamage/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 6 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Aviation/>
            <Ordnance/>
            <Guns/>
            <Damage/>
            <Reload/>
            <Light/>
            <Medium/>
            <Heavy/>
            <AntiAirDPS/>
            <AntiAirBurst/>
            <Speed/>
            <CrashDamage/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 7 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Firepower/>
            <AntiAir/>
            <Hit/>
            <Damage/>
            <Reload/>
            <AntiAirDPS/>
            <Range/>
            <Nation/>
          </tr>
        </thead>
      )
    };

    if ( equipment_template === 8 ) {
      return (
        <thead>
          <tr>
            <Icon/>
            <Equipment/>
            <Stars/>
            <ASW/>
            <Accuracy/>
            <Damage/>
            <Reloads/>
            <DPS/>
            <Range/>
            <Radius/>
            <Type/>
            <Notes/>
          </tr>
        </thead>
      )
    };
  };
};