export default function ShipGet(api, type="main") 
{
	let main_ship		= [];
	let vanguard_ship	= [];

	api.forEach((ship) => {
		let main		= false;
		let vanguard	= false;

		if(ship.hullType === "Battleship")			{main = true};
		if(ship.hullType === "Battlecruiser")		{main = true};
		if(ship.hullType === "Aviation Battleship")	{main = true};
		if(ship.hullType === "Aircraft Carrier")	{main = true};
		if(ship.hullType === "Light Carrier")		{main = true};
		if(ship.hullType === "Repair")				{main = true};
		if(ship.hullType === "Monitor")				{main = true};

		if(ship.hullType === "Large Cruiser")	{vanguard = true};
		if(ship.hullType === "Heavy Cruiser")	{vanguard = true};
		if(ship.hullType === "Light Cruiser")	{vanguard = true};
		if(ship.hullType === "Destroyer")		{vanguard = true};
		if(ship.hullType === "Munition Ship")	{vanguard = true};

		if(ship.id === "30001") ship.names.en = "Hiryuu META";
		if(ship.id === "30002") ship.names.en = "Ark Royal META";

		if ( main ) main_ship.push(ship);
		if ( vanguard ) vanguard_ship.push(ship);
	});

	main_ship.sort((a, b) => {
		const name_a = a.names.en.toLowerCase();
		const name_b = b.names.en.toLowerCase();

		if ( name_a < name_b ) return -1;
		if ( name_a > name_b ) return 1;

		return 0;
	});
	
	vanguard_ship.sort((a, b) => {
		const name_a = a.names.en.toLowerCase();
		const name_b = b.names.en.toLowerCase();

		if ( name_a < name_b ) return -1;
		if ( name_a > name_b ) return 1;

		return 0;
	});
	if ( type === "main" ) return main_ship;
	if ( type === "vanguard" ) return vanguard_ship;
};