import React, { Component } from "react";

export default class ImgR extends Component {
  render() {
    const link = this.props.src;
    const id   = this.props.id;
    return (
      <img 
        id={id}
        src={link}
        referrerPolicy="no-referrer"
        alt="..."
        className="img-fluid ImgR-Icon"
      />
    );
  };
};