import React, { Component } from "react";
import { Modal, ModalBody, Table } from "reactstrap";

// function
import RarityGet from "../../../components/RarityGet.js";

// core components
import ModalHeader from "./FleetModal/ModalHeader.js";
import Ship from "./FleetModal/Ship.js";
import Reload from "./FleetModal/Reload.js";
import Equipment from "./FleetModal/Equipment.js";

export default class FleetModal extends Component {
  render() {
    const azurapi = this.props.azurapi;
    const banner  = this.props.banner;
    const stats   = this.props.stats;  
    const gear    = this.props.gear;

    const modalData  = this.props.modalData;
    const modalOpen  = this.props.modalOpen;
    const slot       = this.props.slot;

    const openGearModal = this.props.openGearModal;
    const gearData      = this.props.gearData;
    const gearSlot      = this.props.gearSlot;

    let ship_slot    = modalData.slot1;
    let gear_slot    = gearData.slot1;

    if ( slot === 1 ) { ship_slot = modalData.slot1; gear_slot = gearData.slot1 };
    if ( slot === 2 ) { ship_slot = modalData.slot2; gear_slot = gearData.slot2 };
    if ( slot === 3 ) { ship_slot = modalData.slot3; gear_slot = gearData.slot3 };

    // current slot extract
    let ship_id     = ship_slot.id;
    let ship_name   = ship_slot.name;
    let ship_banner = ship_slot.banner;
    let ship_type   = ship_slot.type;
    let ship_kai    = ship_slot.kai;
    let ship_level  = ship_slot.level;
    let ship_oath   = ship_slot.oath;
    let ship_rarity = ship_slot.rarity;
    let ship_reload = ship_slot.reload;
    let ship_tech   = ship_slot.tech;
    let ship_cat    = ship_slot.cat;

    let data_build  = {
      id: ship_id,
      name: ship_name,
      banner: ship_banner,
      type: ship_type,
      kai: ship_kai,
      level: ship_level,
      oath: ship_oath,
      rarity: ship_rarity,
      reload: ship_reload,
      tech: ship_tech,
      cat: ship_cat
    };

    // current slot
    let final_reload = ship_reload + ship_tech + ship_cat;

    // input event get_ship_option
    const get_ship_option = (event) => {
      const option_value = event.target.value;
      const ship_data    = azurapi.find(idx => idx.id === option_value);
      const banner_data  = banner.find(idx => idx.id === option_value);

      if ( banner_data ) ship_banner = banner_data.skins[0].banner;

      let primary_type;
      let secondary_type;
      let tertiary_type;

      let primary_max;
      let secondary_max;
      let tertiary_max;

      let primary_effficiency;
      let secondary_effficiency;
      let tertiary_effficiency;

      if ( ship_data )
      {
        ship_id     = ship_data.id;
        ship_name   = ship_data.names.en;
        ship_type   = ship_data.hullType;
        ship_rarity = ship_data.rarity;
        ship_reload = ship_data.stats.level125.reload;

        primary_type   = ship_data.slots[0].type;
        secondary_type = ship_data.slots[1].type;
        tertiary_type  = ship_data.slots[2].type;

        primary_max    = ship_data.slots[0].max;
        secondary_max  = ship_data.slots[1].max;
        tertiary_max   = ship_data.slots[2].max;

        primary_effficiency   = ship_data.slots[0].maxEfficiency;
        secondary_effficiency = ship_data.slots[1].maxEfficiency;
        tertiary_effficiency  = ship_data.slots[2].maxEfficiency;

        if ( ship_data.retrofit )
        {
          ship_kai = { status: true, availability: true };
          if ( banner_data && banner_data.skins[1].name === "Retrofit" )
          {
            ship_banner = banner_data.skins[1].banner;
          };
          ship_rarity = RarityGet(ship_rarity, 3);
          ship_reload = ship_data.stats.level125Retrofit.reload;
        }
        else
        {
          ship_kai = { status: false, availability: false };
        };

      }
      else
      {
        ship_id     = "000";
        ship_name   = "Unknown";
        ship_banner = "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png";
        ship_type   = "Unknown-Type";
        ship_kai    = { status: false, availability: false };
        ship_rarity = "Unknown-Rarity"
        ship_reload = 0;
        ship_tech   = 0;
        ship_cat    = 0;

        primary_type   = "Primary";
        secondary_type = "Secondary";
        tertiary_type  = "Tertiary";

        primary_max    = 0;
        secondary_max  = 0;
        tertiary_max   = 0;

        primary_effficiency   = 0;
        secondary_effficiency = 0;
        tertiary_effficiency  = 0;
      };

      ship_level = "125";
      ship_oath  = false;

      document.getElementById("slot-ship-banner").src       = ship_banner;
      document.getElementById("slot-ship-name").innerHTML   = ship_name;
      document.getElementById("slot-ship-type").innerHTML   = ship_type;
      document.getElementById("slot-ship-rarity").innerHTML = ship_rarity;
      document.getElementById("equipment-type-1").innerHTML = primary_type;
      document.getElementById("equipment-type-2").innerHTML = secondary_type;
      document.getElementById("equipment-type-3").innerHTML = tertiary_type;
      document.getElementById("equipment-max-1").innerHTML  = primary_max;
      document.getElementById("equipment-max-2").innerHTML  = secondary_max;
      document.getElementById("equipment-max-3").innerHTML  = tertiary_max;
      document.getElementById("equipment-efficiency-1").innerHTML = primary_effficiency + "%";
      document.getElementById("equipment-efficiency-2").innerHTML = secondary_effficiency + "%";
      document.getElementById("equipment-efficiency-3").innerHTML = tertiary_effficiency + "%";

      if ( ship_kai.status ) document.getElementById("slot-ship-name").append(" (Retrofit)");

      document.getElementById("reload-base").value  = ship_reload;
      document.getElementById("reload-tech").value  = ship_tech;
      document.getElementById("reload-cat").value   = ship_cat;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);

      // deselect all
      document.getElementById("retrofit-select-true").removeAttribute("selected");
      document.getElementById("retrofit-select-false").removeAttribute("selected");
      document.getElementById("level-select-125").removeAttribute("selected");
      document.getElementById("level-select-120").removeAttribute("selected");
      document.getElementById("level-select-100").removeAttribute("selected");
      document.getElementById("oath-select-true").removeAttribute("selected");
      document.getElementById("oath-select-false").removeAttribute("selected");

      // select
      if ( ship_kai.availability )
      {
        document.getElementById("retrofit-select").removeAttribute("disabled");
        document.getElementById("retrofit-select-true").setAttribute("selected", "");
      }
      else
      {
        document.getElementById("retrofit-select").setAttribute("disabled", "");
        document.getElementById("retrofit-select-false").setAttribute("selected", "");
      };

      document.getElementById("level-select-125").setAttribute("selected", "");
      document.getElementById("oath-select-false").setAttribute("selected", "");

      // build
      data_build  = {
        azurapi: ship_data,
        id: ship_id,
        name: ship_name,
        banner: ship_banner,
        type: ship_type,
        kai: ship_kai,
        level: ship_level,
        oath: ship_oath,
        rarity: ship_rarity,
        reload: ship_reload,
        tech: ship_tech,
        cat: ship_cat
      };

      console.log(data_build);
    };

    // function get_oath_reload => extends get_ship_reload
    const get_oath_reload = (stats, level, kai=false) => {
      if ( kai )
      {
        const reload   = stats.retrofit_stats.reload;
        const growth   = stats.retrofit_growth.reload;
        const enhance  = stats.retrofit_enhance.reload;
        const retrofit = stats.retrofit.reload;

        return retrofit + Math.floor( ( reload + ( ( growth * ( level - 1 ) ) / 1000 ) + enhance ) * 1.12 );
      }
      else
      {
        const reload   = stats.stats.reload;
        const growth   = stats.growth.reload;
        const enhance  = stats.enhance.reload;

        return Math.floor( ( reload + ( ( growth * ( level - 1 ) ) / 1000 ) + enhance ) * 1.12 );
      };
    };

    // function get_ship_reload => extends get_level_option, get_retrofit_option, get_oath_option
    const get_ship_reload = () => {
      const ship_data  = data_build.azurapi;
      const stats_data = stats.find(idx => idx.id === ship_id);
      let return_value = 0;

      if ( !ship_data || !stats_data ) return ship_reload;

      if ( ship_kai.status )
      {
        if ( ship_level === "125" ) return_value = ship_data.stats.level125Retrofit.reload;
        if ( ship_level === "120" ) return_value = ship_data.stats.level120Retrofit.reload;
        if ( ship_level === "100" ) return_value = ship_data.stats.level100Retrofit.reload;
        if ( ship_oath )
        {
          if ( ship_level === "125" ) return_value = get_oath_reload(stats_data, 125, true);
          if ( ship_level === "120" ) return_value = get_oath_reload(stats_data, 120, true);
          if ( ship_level === "100" ) return_value = get_oath_reload(stats_data, 100, true);
        };
      }
      else
      {
        if ( ship_level === "125" ) return_value = ship_data.stats.level125.reload;
        if ( ship_level === "120" ) return_value = ship_data.stats.level120.reload;
        if ( ship_level === "100" ) return_value = ship_data.stats.level100.reload;
        if ( ship_oath )
        {
          if ( ship_level === "125" ) return_value = get_oath_reload(stats_data, 125);
          if ( ship_level === "120" ) return_value = get_oath_reload(stats_data, 120);
          if ( ship_level === "100" ) return_value = get_oath_reload(stats_data, 100);
        };
      };
      return return_value;
    };

    // input event get_level_option => extends #level-select
    const get_level_option = (event) => {
      ship_level        = event.target.value;
      data_build.level  = ship_level;
      ship_reload       = get_ship_reload();
      data_build.reload = ship_reload;
      document.getElementById("reload-base").value = ship_reload;
    };

    // input event get_retrofit-option => extends #retrofit-select
    const get_retrofit_option = (event) => {
      const option_value = event.target.value;
      const banner_data  = banner.find(idx => idx.id === ship_id);

      // retrofit true
      if ( option_value === "1" )
      {
        if ( data_build.kai.status ) return;

        data_build.kai.status = true;
        if ( banner_data && banner_data.skins[1].name === "Retrofit" )
        {
          ship_banner = banner_data.skins[1].banner;
          data_build.banner = ship_banner;
        };

        ship_rarity = RarityGet(ship_rarity, 3);
        data_build.rarity = ship_rarity;

        ship_reload = get_ship_reload();
        data_build.reload = ship_reload;

        document.getElementById("reload-base").value = ship_reload;
        document.getElementById("slot-ship-name").append(" (Retrofit)");
      };

      // retrofit false
      if ( option_value === "0" ) {
        if ( !data_build.kai.status ) return;

        data_build.kai.status = false;
        if ( banner_data ) {
          ship_banner = banner_data.skins[0].banner;
          data_build.banner = ship_banner;
        };

        ship_rarity = RarityGet(ship_rarity, 4);
        data_build.rarity = ship_rarity;

        ship_reload = get_ship_reload();
        data_build.reload = ship_reload;

        document.getElementById("reload-base").value        = ship_reload;
        document.getElementById("slot-ship-name").innerHTML = ship_name;
      };

      document.getElementById("slot-ship-banner").src       = ship_banner;
      document.getElementById("slot-ship-rarity").innerHTML = ship_rarity;
    };

    // input event get_oath_option => extends #oath-select
    const get_oath_option = (event) => {
      if ( event.target.value === "1" ) ship_oath = true;
      if ( event.target.value === "0" ) ship_oath = false;
      data_build.oath   = ship_oath;
      ship_reload       = get_ship_reload();
      data_build.reload = ship_reload;
      document.getElementById("reload-base").value = ship_reload;
    };

    // input event input_reload => extends #reload-base
    const input_reload = (event) => {
      ship_reload       = parseInt(event.target.value);
      data_build.reload = ship_reload;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // input event input_reload => extends #reload-tech
    const input_tech = (event) => {
      ship_tech       = parseInt(event.target.value);
      data_build.tech = ship_tech;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // input event input_reload => extends #reload-cat
    const input_cat = (event) => {
      ship_cat       = parseInt(event.target.value);
      data_build.cat = ship_cat;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // close modal output
    const modalClose = () => {
      this.props.modalClose({
        slot: slot,
        ...data_build
      });
    };

    return (
      <Modal isOpen={modalOpen} toggle={modalClose} size="xl" className="pb-5 dark">
        <ModalHeader toggle={modalClose} id="slot-ship-name" banner={ship_banner} name={ship_name} kai={ship_kai} />
        <ModalBody className="text-center align-middle">
          <Ship
            ship_rarity={ship_rarity}
            ship_type={ship_type}
            azurapi={azurapi}
            ship_id={ship_id}
            get_ship_option={get_ship_option}
            ship_level={ship_level}
            get_level_option={get_level_option}
            ship_kai={ship_kai}
            get_retrofit_option={get_retrofit_option}
            ship_oath={ship_oath}
            get_oath_option={get_oath_option}
          />
          <Table className="fleetModalTable">
            <Reload
              input_reload={input_reload}
              input_tech={input_tech}
              input_cat={input_cat}
              ship_reload={ship_reload}
              ship_tech={ship_tech}
              ship_cat={ship_cat}
              final_reload={final_reload}
            />
          </Table>
          <Table dark className="fleetModalTable">
            <Equipment
              data_build={data_build}
              gear={gear}
              gearData={gearData}
              gearSlot={gearSlot}
              openGearModal={openGearModal}
            />
          </Table>
        </ModalBody>
      </Modal>
    );
  };
};