import React, { useState, useEffect } from "react";
import { Route, Routes, useLocation, useNavigate, useParams } from "react-router-dom";

import ProgressLoad from "../components/ProgressLoad.js";

// Equipment Components
import EquipmentRoute from "./Equipment/EquipmentRoute.js";
import EquipmentList from "./Equipment/EquipmentList.js";
import EquipmentType from "./Equipment/EquipmentType.js";

function Equipment() {
  const { pathname }            = useLocation();
  const [error, setError]       = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurAPI, setAzurAPI]   = useState([]);
  let navigate                  = useNavigate();

  useEffect(() => {
    if (pathname) window.scrollTo(0, 0);
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/equipments.json")
    .then(res => res.json())
    .then(
      (result) => {
        setIsLoaded(true);
        setAzurAPI(result);
      },
      (error) => {
        setIsLoaded(true);
        setError(error);
      }
    );
  }, [pathname])

  const equipment_navigate = (equipment) => {
    console.log(equipment)
    navigate(equipment);
  };

  // this should make new class component but its in parent class so fine
  const EquipmentRouteMatch = () => {
    const { id }        = useParams();
    const gear_array    = [
      'destroyer_guns',
      'light_cruiser_guns',
      'heavy_cruiser_guns',
      'large_cruiser_guns',
      'battleship_guns',
      'ship_torpedoes',
      'submarine_torpedoes',
      'fighter_planes',
      'dive_bomber_planes',
      'torpedo_bomber_planes',
      'seaplanes',
      'anti-air_guns',
      'anti-submarine_equipment'
    ];
    let id_azurapi      = id.toLowerCase();
    let match_type_list = false;
    const equipmentData = azurAPI.find(equip => equip.id.toLowerCase().replace(/\s+/g, "_") === id_azurapi);

    gear_array.forEach((idx) => {if ( id_azurapi === idx ) { match_type_list = idx } });
    if ( match_type_list ) {
      return <EquipmentType azurAPI={azurAPI} navigate={equipment_navigate} type={match_type_list} />
    } else {
      return <EquipmentRoute equipmentData={equipmentData} paramsId={id} />
    };
  };

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <ProgressLoad text="Loading AzurAPI..." />
  } else {
    return (
      <>
        <Routes>
          <Route path=":id" element={<EquipmentRouteMatch />} />
          <Route path="" element={<EquipmentList />} />
        </Routes>
      </>
    );
  }
}

export default Equipment;