import React, { Component } from "react";
import { Input } from "reactstrap";

export default class Pick extends Component {
  render() {
    const value   = this.props.defaultValue;
    const change  = this.props.onChange;
    const azurapi = this.props.azurapi;

    return (
      <Input 
        type="select"
        name="ship-select"
        id="ship-select"
        className="input_max"
        defaultValue={value}
        onChange={change}
        valid
      >
        <option value="0">Unknown</option>
        {azurapi.map((ship, key) => {
          return (<option key={key} value={ship.id}>{ship.names.en}</option>);
        })}
      </Input>
    );
  };
};