import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Raw extends Component {
  render() {
    return (
      <>
        <th id="templateHead-rawdps" className="dps-r">Raw</th>
        <UncontrolledTooltip placement="top" target="templateHead-rawdps" delay={0}><b>Raw DPS</b></UncontrolledTooltip>
      </>
    );
  };
};