import React, { Component } from "react";
import { Table } from 'reactstrap';

import Fits from "./EquipmentFits/Fits.js";
import Status from "./EquipmentFits/Status.js";
import Tooltips from "./EquipmentFits/Tooltips.js";

import typeDD  from "../../../assets/AL/DD.png";
import typeCL  from "../../../assets/AL/CL.png";
import typeCA  from "../../../assets/AL/CA.png";
import typeBM  from "../../../assets/AL/BM.png";
import typeCB  from "../../../assets/AL/CB.png";
import typeBB  from "../../../assets/AL/BB.png";
import typeBC  from "../../../assets/AL/BC.png";
import typeBBV from "../../../assets/AL/BBV.png";
import typeCV  from "../../../assets/AL/CV.png";
import typeCVL from "../../../assets/AL/CVL.png";
import typeAR  from "../../../assets/AL/AR.png";
import typeAE  from "../../../assets/AL/AE.png";
import typeSS  from "../../../assets/AL/SS.png";
import typeSSV from "../../../assets/AL/SSV.png";

export default class EquipmentFits extends Component {
  render() {
    const equipment_fits     = this.props.fits;
    const fits_DD            = equipment_fits.destroyer;
    const fits_CL            = equipment_fits.lightCruiser;
    const fits_CA            = equipment_fits.heavyCruiser;
    const fits_BM            = equipment_fits.monitor;
    const fits_CB            = equipment_fits.largeCruiser;
    const fits_BB            = equipment_fits.battleship;
    const fits_BC            = equipment_fits.battlecruiser;
    const fits_BBV           = equipment_fits.aviationBattleship;
    const fits_CV            = equipment_fits.aircraftCarrier;
    const fits_CVL           = equipment_fits.lightCarrier;
    const fits_AR            = equipment_fits.repairShip;
    const fits_AE            = equipment_fits.munitionShip;
    const fits_SS            = equipment_fits.submarine;
    const fits_SSV           = equipment_fits.submarineCarrier;
    const destroyer          = Status(fits_DD);
    const lightCruiser       = Status(fits_CL);
    const heavyCruiser       = Status(fits_CA);
    const monitor            = Status(fits_BM);
    const largeCruiser       = Status(fits_CB);
    const battleship         = Status(fits_BB);
    const battlecruiser      = Status(fits_BC);
    const aviationBattleship = Status(fits_BBV);
    const aircraftCarrier    = Status(fits_CV);
    const lightCarrier       = Status(fits_CVL);
    const repairShip         = Status(fits_AR);
    const munitionShip       = Status(fits_AE);
    const submarine          = Status(fits_SS);
    const submarineCarrier   = Status(fits_SSV);

    let tips_DD  = "";
    let tips_CL  = "";
    let tips_CA  = "";
    let tips_BM  = "";
    let tips_CB  = "";
    let tips_BB  = "";
    let tips_BC  = "";
    let tips_BBV = "";
    let tips_CV  = "";
    let tips_CVL = "";
    let tips_AR  = "";
    let tips_AE  = "";
    let tips_SS  = "";
    let tips_SSV = "";

    if ( destroyer === "fits-secondary" )          { tips_DD  = <Tooltips target="DD-fits" text={fits_DD}/> };
    if ( lightCruiser === "fits-secondary" )       { tips_CL  = <Tooltips target="CL-fits" text={fits_CL}/> };
    if ( heavyCruiser === "fits-secondary" )       { tips_CA  = <Tooltips target="CA-fits" text={fits_CA}/> };
    if ( monitor === "fits-secondary" )            { tips_BM  = <Tooltips target="BM-fits" text={fits_BM}/> };
    if ( largeCruiser === "fits-secondary" )       { tips_CB  = <Tooltips target="CB-fits" text={fits_CB}/> };
    if ( battleship === "fits-secondary" )         { tips_BB  = <Tooltips target="BB-fits" text={fits_BB}/> };
    if ( battlecruiser === "fits-secondary" )      { tips_BC  = <Tooltips target="BC-fits" text={fits_BC}/> };
    if ( aviationBattleship === "fits-secondary" ) { tips_BBV = <Tooltips target="BBV-fits" text={fits_BBV}/> };
    if ( aircraftCarrier === "fits-secondary" )    { tips_CV  = <Tooltips target="CV-fits" text={fits_CV}/> };
    if ( lightCarrier === "fits-secondary" )       { tips_CVL = <Tooltips target="CVL-fits" text={fits_CVL}/> };
    if ( repairShip === "fits-secondary" )         { tips_AR  = <Tooltips target="AR-fits" text={fits_AR}/> };
    if ( munitionShip === "fits-secondary" )       { tips_AE  = <Tooltips target="AE-fits" text={fits_AE}/> };
    if ( submarine === "fits-secondary" )          { tips_SS  = <Tooltips target="SS-fits" text={fits_SS}/> };
    if ( submarineCarrier === "fits-secondary" )   { tips_SSV = <Tooltips target="SSV-fits" text={fits_SSV}/> };

    let ext = "";
    if ( fits_CL === "Tien Hai excluded" ) { ext = " fits-primary" };
    return (
      <>
        <Table hover className="desktop-view">
          <thead>
            <tr>
              <Fits className={destroyer}          id="DD-fits"  src={typeDD} />
              <Fits className={lightCruiser + ext} id="CL-fits"  src={typeCL} />
              <Fits className={heavyCruiser}       id="CA-fits"  src={typeCA} />
              <Fits className={monitor}            id="BM-fits"  src={typeBM} />
              <Fits className={largeCruiser}       id="CB-fits"  src={typeCB} />
              <Fits className={battleship}         id="BB-fits"  src={typeBB} />
              <Fits className={battlecruiser}      id="BC-fits"  src={typeBC} />
              <Fits className={aviationBattleship} id="BBV-fits" src={typeBBV} />
              <Fits className={aircraftCarrier}    id="CV-fits"  src={typeCV} />
              <Fits className={lightCarrier}       id="CVL-fits" src={typeCVL} />
              <Fits className={repairShip}         id="AR-fits"  src={typeAR} />
              <Fits className={munitionShip}       id="AE-fits"  src={typeAE} />
              <Fits className={submarine}          id="SS-fits"  src={typeSS} />
              <Fits className={submarineCarrier}   id="SSV-fits" src={typeSSV} />
            </tr>
          </thead>
        </Table>
        <Table className="mobile-view">
          <thead>
            <tr>
              <Fits className={destroyer}          id="DD-fits"  src={typeDD} />
              <Fits className={lightCruiser + ext} id="CL-fits"  src={typeCL} />
              <Fits className={heavyCruiser}       id="CA-fits"  src={typeCA} />
              <Fits className={monitor}            id="BM-fits"  src={typeBM} />
            </tr>
            <tr>
              <Fits className={largeCruiser}       id="CB-fits"  src={typeCB} />
              <Fits className={battleship}         id="BB-fits"  src={typeBB} />
              <Fits className={battlecruiser}      id="BC-fits"  src={typeBC} />
              <Fits className={aviationBattleship} id="BBV-fits" src={typeBBV} />
            </tr>
            <tr>
              <Fits className={aircraftCarrier}    id="CV-fits"  src={typeCV} />
              <Fits className={lightCarrier}       id="CVL-fits" src={typeCVL} />
              <Fits className={repairShip}         id="AR-fits"  src={typeAR} />
              <Fits className={munitionShip}       id="AE-fits"  src={typeAE} />
            </tr>
            <tr>
              <Fits className={submarine}          id="SS-fits"  src={typeSS} />
              <Fits className={submarineCarrier}   id="SSV-fits" src={typeSSV} />
              <Fits className="fits-null" none />
              <Fits className="fits-null" none />
            </tr>
          </thead>
        </Table>
        {tips_DD}
        {tips_CL}
        {tips_CA}
        {tips_BM}
        {tips_CB}
        {tips_BB}
        {tips_BC}
        {tips_BBV}
        {tips_CV}
        {tips_CVL}
        {tips_AR}
        {tips_AE}
        {tips_SS}
        {tips_SSV}
      </>
    )
  }
}