import React, { Component } from "react";
import { Modal, ModalBody, Table } from "reactstrap";

import ImgR from "../../../components/ImgR.js";

export default class EquipmentModal extends Component {
  render() {
    const gear      = this.props.gear;
    const gearData  = this.props.gearData;
    const gearOpen  = this.props.gearOpen;
    const gearClose = this.props.gearClose;
    const gearSlot  = this.props.gearSlot;

    let equipment_list    = [];
    gear.forEach((api) => {
      if ( api.category ) {
        let current_gear        = api;
        let rarity_container    = [];
        api.tiers.forEach((idx) => { if ( idx ) rarity_container.push(idx.rarity) });
        let equipment_rarity    = 0;
        rarity_container.forEach((x) => {
          if ( x === "Normal"     && equipment_rarity < 1 ) equipment_rarity = 1;
          if ( x === "Rare"       && equipment_rarity < 2 ) equipment_rarity = 2;
          if ( x === "Elite"      && equipment_rarity < 3 ) equipment_rarity = 3;
          if ( x === "Super Rare" && equipment_rarity < 4 ) equipment_rarity = 4;
          if ( x === "Ultra Rare" && equipment_rarity < 5 ) equipment_rarity = 5;
        });
        current_gear = { ...current_gear, "highest_rarity": equipment_rarity };
        if ( equipment_rarity > 1 ) { 
          equipment_list.push(current_gear);
        };
      };
    });
    equipment_list.sort((a,b) => { return b.highest_rarity - a.highest_rarity });

    console.log(gear);
    return (
      <Modal isOpen={gearOpen} toggle={gearClose} size="xl" className="pb-5 dark">
        <ModalBody>
          <Table>
            <thead>
              <tr>
                <th>ICON</th>
                <th>NAME</th>
                <th>TYPE</th>
                <th>FACTIOn</th>
              </tr>
            </thead>
            <tbody>
              {equipment_list.map((api, key) => {
                const rarity = api.highest_rarity;
                let rarity_bg = "equipment_empty"
                if ( rarity === 5 ) rarity_bg = "equipment-rarity-5 m-auto";
                if ( rarity === 4 ) rarity_bg = "equipment-rarity-4 m-auto";
                if ( rarity === 3 ) rarity_bg = "equipment-rarity-3 m-auto";
                if ( rarity === 2 ) rarity_bg = "equipment-rarity-2 m-auto";
                if ( rarity === 1 ) rarity_bg = "equipment-rarity-1 m-auto";

                return (
                  <tr key={key}>
                    <td><div className={rarity_bg}><ImgR src={api.image} /></div></td>
                    <td>{api.id}</td>
                    <td>{api.type.name}</td>
                    <td>{api.nationality}</td>
                  </tr>
                );
              })}
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
    );
  };
};