import React, { Component } from "react";
import { Breadcrumb } from "reactstrap";

// icon load
import "../../assets/css/nucleo-icons.css";

// import part
import EXTEND_HOME from "./extend/home.js";
import EXTEND_SHIP from "./extend/ship.js";
import EXTEND_EQUIPMENT from "./extend/equipment.js";
import EXTEND_PATH from "./extend/path.js";

export default class Navigator extends Component {
  render() {
    let ship      = this.props.ship;
    let equipment = this.props.equipment;
    let path_link = this.props.path;
    let execute   = true;
    let output    = (<EXTEND_HOME />);

    if ( ship && equipment ) execute = false;
    if ( !path_link ) execute = false;
    
    if ( ship && execute )      { output = (<>{output}<EXTEND_SHIP /><EXTEND_PATH path={path_link} /></>) };
    if ( equipment && execute ) { output = (<>{output}<EXTEND_EQUIPMENT /><EXTEND_PATH path={path_link} /></>) };

    return (
      <Breadcrumb>
        {output}
      </Breadcrumb>
    )
  }
};